@import '../../styles/vars.scss';


*,
*:after,
*:before {
    box-sizing: border-box;
}

.container {
    margin-left: 1vw;
    width: 100%;
    max-width: 100%;

}

.tabs {
    display: flex;
    background: linear-gradient(176.59deg, rgba(233, 251, 255, 0) 5.41%, rgba(248, 254, 255, 0) 5.42%, rgba(237, 237, 237, 0.45) 26.05%, rgba(247, 247, 247, 0.45) 79.2%, rgba(255, 255, 255, 0) 97.4%);
    box-shadow: 0px 0px 6px rgba(0, 0, 0, 0.12);
    backdrop-filter: blur(30.5px);
    padding: 0.50rem;
    border-radius: 87px;
    height: 80px;
    width: 33.4vw;

    * {
        z-index: 2;
    }
}

input[type="radio"] {
    display: none;
}

.tab {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 65px;
    font-size: 1.3vw;
    border-radius: 74px;
    cursor: pointer;
    transition: color 0.15s ease-in;
    width: 10.8vw;
}

input[id="radio-1"] {
    &:checked {
        &~.glider {
            transform: translateX(0);
        }
    }
}

input[id="radio-2"] {
    &:checked {
        &~.glider {
            transform: translateX(100%);
        }
    }
}

input[id="radio-3"] {
    &:checked {
        &~.glider {
            transform: translateX(200%);
        }

    }
}

.glider {
    position: absolute;
    display: flex;
    height: 65px;
    width: 10.8vw;
    background-color: $base-white;
    z-index: 1;
    border-radius: 74px;
    transition: 0.25s ease-out;
    box-shadow: 0px 0px 6px rgba(0, 0, 0, 0.12);

}

.content {
    margin: 3rem 0;
    height: 60vh;
    width: 33.4vw;
    background: rgba(237, 237, 237, 0.1);
    box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.12);
    backdrop-filter: blur(34px);
    border-radius: 8px;
    display: flex;
    justify-content: center;
    align-items: center;

    img {
        max-width: 100%;
    }

}





@media (max-width: 1000px) {
    .container {
        .tabs{
            margin-top: 30px;
            width: 96%;
            .tab {
                height: 65px;
                font-size: 10pt;
                width: 33%;
            }
            .glider {                
                height: 65px;
                width: 33%;
            }
        }
.content{
    width: 96%;
}
        
    }
}
@media (max-width: 1200px) and (orientation: portrait){
.container {
        .tabs{
            margin-top: 30px;
            width: 96%;
            .tab {
                height: 65px;
            font-size: larger;
                width: 33%;
            }
            .glider {                
                height: 65px;
                width: 33%;
            }
        }
.content{
    width: 96%;
    max-height: 350px;
}
        
    }
}